.close{
    cursor: pointer;
    float: right;
    width: 2em;
    top: 20;
    margin: 2em;
}

.menu{
    cursor: pointer;
    width: 2.3em;
    height: auto;
    top: 0;
    right: 0;
    position: absolute;
    margin-top: 1.2em;
    margin-right: 0.7em;
}


@media only screen and (min-width: 860px) {
    .header h3{
        margin: 0 !important;
        padding: 0 !important;
        margin-top: 1.2em !important;
        font-size: 1.2rem;
        margin-left: 1.5em !important;
        position: absolute;
    }
    .container{
        justify-content: center;
        align-items: center;
        display: flex;
    }
    header{
        position: unset !important;
        top: 0;
        z-index: 999;
        height: 75px !important;
        width: 95%;
        background-color: #ffffff;
        border-radius: 40px;
    }
    .menu{
        display: none;
    }
    .close{
        display: none;
    }
    nav{
        top: 0;
        right: 0;
        position: unset !important;
        height: unset !important;
        display: flex;
        transform: translateX(0) !important;
        justify-content: center;
        align-items: center;
        background: none;
        margin-left: auto;

        ul{
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 !important;
            margin-top: 1.9em !important;

            a{
                margin: 0 !important;
                padding: 0 !important;
                margin-left: 1.5em !important;
                margin-right: 1.8em !important;
                font-size: 0.7rem;
                padding-left: 0 !important;
            }
        }
    }

    button{
        margin-right: 2em !important;
    }

